/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .post_container{
    margin-bottom: 70px;
  }
  img{
    max-width: 690px;
  }
  .post_block{
    max-width: 690px;
  }
  .post_date{
    margin: 0 0 20px 0;
    padding: 12px 18px;
    span{
      display: inline-block;
    }
    .date_num{
      font: 24px/28px $OSBold, Arial, Helvetica, sans-serif;
      margin: 0 5px 0 0;
    }
  }
  .read_more{
    margin: 0 25px 0 0;
  }
  .numeration_block{
    margin: 0;
  }
  .recent-post_box{
    li::before{
      top: 0;
    }
  }
  .comment_content{
    max-width: 569px;
  }
  .form-comment{
    textarea{
      width: 629px !important;
    }
    .comment-name,
    .comment-email{
      width: 300px !important;
    }
    .form-checkbox:not(checked) + label{
      top: 9px;
    }
    .form-checkbox:not(checked) + label::before{
      top: -3px;
    }
    input[type="submit"]{
      margin-right: 60px;
    }
  }
  .sidebar{
    padding: 18px 0 0 10px;
  }
  .search{
    a{
      right: 0;
    }
  }
  #search{
    input[type="search"]{
      width: 190px !important;
    }
  }
  .subscribe_box{
    input[type="text"]{
      width: 245px !important;
    }
  }
  .categories_box{
    li::before{
      padding-right: 10px;
    }
    li{
      padding: 0 7px;
    }
  }
  .recent-post_box{
    li::before{
      //margin: 0 10px 0 0;
    }
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  .sidebar{
    padding: 18px 10px 0 10px;
  }
  #search{
    input[type="search"] {
      width: 100% !important;
    }
  }
  .subscribe_box{
    input[type="text"] {
      width: 100% !important;
      display: block;
    }
    input[type="submit"] {
      display: block;
      float: left;
    }
  }
  .popular-post_box{
    text-align: center;
    img{
      margin: 0 auto 10px;
    }
    time{
      float: left;
    }
  }
}


/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
  $title-font-size: #{38px}/#{42px};
  $primary-font-size: #{13px}/#{17px};
  $second-font-size: #{14px}/#{24px};
  $primary-heading-font-size: #{22px}/#{26px};
  $second-heading-font-size: #{22px}/#{38px};
  $third-heading-font-size: #{23px}/#{28px};
  $aside-font-size: #{15px}/#{19px};
  $datebox-font-size: #{17px}/#{21px};
  $quote-font-size: #{18px}/#{24px};
  .main_contant{
    padding-right: 15px;
  }
  .hamburger{
    display: block;
  }
  #nav{
    position: absolute;
    z-index:20;
    top: 77px;
    right: 10px;
    background: #fff;
    margin: 0;
    ul{
      display: none;
      li{
        display: block;
        text-align: center;
        &:hover{
          background: #f8f9fb;
          a{
            color: $buttons-color-turquoise;
          }
        }
        a{
          display: inline-block;
          padding: 10px 50px;
          &:hover{
            border-bottom: none;
            background: none;
          }
        }
      }
    }
  }
  .post_block{
    max-width: 100%;
    img{
      max-width: 100%;
    }
  }
  .share_box{
    header{
      left: 20%;
      right: 20%;
      padding: 0;
    }
  }
  .box_avatar{
    margin: 0 10px 20px 0;
  }
  .comment_content{
    max-width: 75%;
  }
  .box_comment_plus{
    .comment_content{
      max-width: 75%;
    }
  }
  .box_plus{
    margin: 0 10px 20px 0;
  }
  .form-comment{
    textarea{
      width: 100% !important;
    }
    .comment-name,
    .comment-email{
      width: 100% !important;
    }
    .form-checkbox:not(checked) + label{
      top: 9px;
      float: none;
      display: block;
      margin-bottom: 35px;
    }
    input[type="submit"]{
      margin-right: 60px;
      float: none;
    }
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
  .share_box{
    padding: 60px 0 20px 0;
    header{
      left: 15%;
      right: 15%;
      top: -18px;
    }
    .social-likes__widget{
      margin-right: 30px;
    }
  }
  .nav_box{
    height: 100px;
    .prev_button,
    .next_button{
      position: relative;
      span{
        position: absolute;
        top: 70px;
      }
    }
    .prev_button{
      span{
        left: 10px;
      }
    }
  }
  .box_avatar{
    margin: 0 auto 26px;
    display: block;
    float: none;
  }
  .comment_content {
    max-width: 100%;
  }
  .box_plus {
    margin: 0 auto 26px;
    float: none;
    display: block;
  }
  .box_comment_plus {
    .comment_content {
      max-width: 100%;
    }
  }
  .form-comment {
    .form-checkbox:not(checked) + label::before {
      top: 4px;
    }
  }
}

