//colors
$primary-text-color: #414a5a;
$second-text-color: #fff;
$third-text-color: #bababa;
$nav-text-color: #484c55;
$buttons-grey-cl: #485460;
$buttons-color-turquoise: #49cbcd;
$input-border-color: #EAEAEA;
$date-cl: #44c0c2;
$link-cl: #a88fd8;

//fonts
$OSRegular: "OpenSansRegular";
$OSSemibold: "OpenSansSemibold";
$OSBold: "OpenSansBold";
$OSLight: "OpenSansLight";
$OSItalic: "OpenSansItalic";
$font-RM: "RobotoMedium";
$title-font-size: #{42px}/#{46px};
$primary-font-size: #{13px}/#{17px};
$second-font-size: #{14px}/#{24px};
$primary-heading-font-size: #{26px}/#{30px};
$second-heading-font-size: #{24px}/#{40px};
$third-heading-font-size: #{23px}/#{28px};
$aside-font-size: #{15px}/#{19px};
$datebox-font-size: #{17px}/#{21px};
$quote-font-size: #{18px}/#{24px};

.aside-text {
  font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
  color: #757b85;
}

.clr_ln:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}

.wrapper_page {
  overflow: hidden;
}

a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}

#arrow_up {
  position: fixed;
  cursor: pointer;
  right: 10px;
  bottom: 30px;
  width: 50px;
  height: 50px;
  background: $buttons-color-turquoise;
  color: $second-text-color;
  text-align: center;
  border-right: 0 solid;
  border-radius: 25px;
  padding: 5px 0 0 0;
  z-index: 30;
  &:hover {
    background: #009C9E;
  }
}

#header {
  .container {
    padding-top: 31px;
    padding-bottom: 31px;
  }
}

#logo {
  float: left;
}

.hamburger {
  display: none;
  float: right;
  .line {
    width: 40px;
    height: 5px;
    background-color: $buttons-color-turquoise;
    display: block;
    margin: 8px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  &:hover {
    cursor: pointer;
  }
}

#hamburger.is-active {
  .line:nth-child(1) {
    -webkit-transform: translateY(13px) rotate(45deg);
    -ms-transform: translateY(13px) rotate(45deg);
    -o-transform: translateY(13px) rotate(45deg);
    transform: translateY(13px) rotate(45deg);
  }
  .line:nth-child(2) {
    opacity: 0;
  }
  .line:nth-child(3) {
    -webkit-transform: translateY(-13px) rotate(-45deg);
    -ms-transform: translateY(-13px) rotate(-45deg);
    -o-transform: translateY(-13px) rotate(-45deg);
    transform: translateY(-13px) rotate(-45deg);
  }
}

#nav {
  float: right;
  margin: 10px 0 0 0;
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 0;
    letter-spacing: 0;
    line-height: 0;
    li {
      display: inline-block;
      vertical-align: top;
      a {
        text-decoration: none;
        color: $nav-text-color;
        padding: 40px 20px 35px 20px;
        font: 15px/19px $OSRegular, Arial, Helvetica, sans-serif;
        &:hover {
          color: $buttons-color-turquoise;
          border-bottom: 3px solid $buttons-color-turquoise;
          background: #f8f9fb;
        }
      }
    }
  }
}

.title_bg {
  margin: 0 auto;
  max-width: 1440px;
  background: url(../img/title_bg.png) top center no-repeat;
  background-size: cover;
}

.title_cont {
  padding: 68px 0 60px;
  margin-bottom: 75px;
}

.title_content {
  text-align: center;
  h1 {
    font: $title-font-size $font-RM, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
}

.main_contant {
  margin-bottom: 88px;
  padding-right: 40px;
}

.post_container {
  margin-bottom: 89px;
}

.post_date {
  float: left;
  padding: 18px;
  background: $buttons-color-turquoise;
  border: 0 solid;
  border-radius: 2px;
  margin: 0 21px 0 0;
  span {
    display: block;
    text-align: center;
  }
  .date_num {
    font: 30px/34px $OSBold, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
  .date_mon {
    font: $datebox-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
  .date_year {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
}

.post_block {
  max-width: 731px;
  float: left;
  font: $primary-font-size $OSRegular, Arial, Helvetica, sans-serif;
  line-height: 24px;
  color: $primary-text-color;
  h1 {
    font: $primary-heading-font-size $font-RM, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    margin: 0 0 14px 0;
  }
  h2 {
    font: $second-heading-font-size $OSLight, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
  }
  img {
    float: left;
    margin: 0 26px 10px 0;
  }
  .post_img {
    margin: 0 0 42px 0;
  }
  p {
    margin: 0 0 34px 0;
  }
  .post_quote {
    blockquote {
      position: relative;
      margin-top: 70px;
      margin-bottom: 97px;
      q {
        quotes: "\201c" "\201d";
        margin: 0 0 22px 0;
        display: inline-block;
        font: $quote-font-size $OSItalic, Arial, Helvetica, sans-serif;
        color: $primary-text-color;
      }
      q::before {
        content: open-quote;
        position: absolute;
        left: -30px;
        font-size: 60px;
      }
      q::after {
        content: no-close-quote;
      }
      cite {
        float: right;
        font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
        color: $primary-text-color;
      }
    }
  }
  div {
    margin-bottom: 26px;
  }
  aside {
    margin-bottom: 33px;
    div {
      padding: 15px 0 13px 0;
      margin-bottom: 0;
      border-top: 1px solid $input-border-color;
      border-bottom: 1px solid $input-border-color;
      span {
        display: inline-block;
      }
    }
  }
}

.read_more {
  @extend .aside-text;
  margin: 0 40px 0 0;
  a {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $link-cl;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}

.num_comments {
  margin: 0 43px 0 0;
  @extend .read_more;
}

.tags_post_box {
  margin: 0 31px 0 0;
  @extend .aside-text;
}

.share_set {
  margin: 0 22px 0 0;
  @extend .aside-text;
}

.post_twitter,
.post_facebook {
  margin: 0 10px 0 0;
  a {
    padding: 7px;
    &:hover {
      .fa-twitter {
        color: #00c3f5;
      }
      .fa-facebook {
        color: #23599a;
      }
    }
  }
  .fa-twitter,
  .fa-facebook {
    font-size: 20px;
    color: #dde1e6;
  }
}

.primary-title-set {
  font: $third-heading-font-size $OSRegular, Arial, Helvetica, sans-serif;
  color: $primary-text-color;
}

.share_box {
  border: 1px solid #ECECEC;
  text-align: center;
  padding: 40px 0 20px 0;
  margin-bottom: 55px;
  position: relative;
  header {
    h1 {
      @extend .primary-title-set;
    }
    position: absolute;
    top: -15px;
    left: 27%;
    right: 27%;
    padding: 0 20px 0 15px;
    display: inline-block;
    background: #fff;
  }
  .social-likes {
    display: block;
    margin: 0;
  }
  .social-likes__widget {
    margin-right: 60px;
    margin-bottom: 0;
    &:last-child {
      margin-right: 0;
    }
  }
}

.nav_box {
  font: 20px/24px $OSRegular, Arial, Helvetica, sans-serif;
  color: $primary-text-color;
  margin: 0 0 55px 0;
  .prev_button,
  .next_button {
    display: table;
    span,
    a {
      display: table-cell;
      vertical-align: middle;
    }
    a {
      display: inline-block;
      padding: 6px 20px;
      color: #c9d1d5;
      background: #F5FAFB;
      border: 0 solid;
      border-radius: 25px;
      &:hover {
        background: #DEFBFB;
        color: #a9b1b5;
      }
    }
  }
  .prev_button {
    float: left;
    a {
      margin: 0 18px 0 0;
    }
  }
  .next_button {
    float: right;
    a {
      margin: 0 0 0 18px;
    }
  }
}

.recent-post_box {
  margin: 0 0 50px 0;
  h1 {
    @extend .primary-title-set;
    margin: 0 0 26px 0;
  }
  ul {
    padding: 0;
    margin: 0 0 0 25px;
    list-style: none;
  }
  li::before {
    content: "\2022";
    position: absolute;
    left: -25px;
    color: $buttons-color-turquoise;
    font-size: 30px;
    vertical-align: middle;
  }
  li {
    position: relative;
    vertical-align: middle;
    padding: 0 2px;
  }
  a {
    font: 14px/26px $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    text-decoration: none;
    display: inline-block;
  }
}

.block_comments {
  h1 {
    @extend .primary-title-set;
    margin: 0 0 40px 0;
  }
}

.box_comment {
  border-bottom: 1px solid $input-border-color;
  margin: 0 0 34px 0;
  header {
    margin: 0 0 15px 0;
  }
  &:last-child {
    border-bottom: none;
    margin: 0 0 72px 0;
  }
}

.box_avatar {
  width: 93px;
  height: 93px;
  margin: 0 28px 20px 0;
  position: relative;
  display: inline-block;
  text-align: center;
  background: #ececec;
  border: 0px solid;
  border-radius: 55px;
  float: left;
  i {
    position: absolute;
    bottom: -13px;
    left: 15px;
    font-size: 90px;
    margin-bottom: -6px;
    color: #ffffff;
  }
}

.comment_content {
  float: right;
  max-width: 610px;
  margin: 0 0 40px 0;
  p {
    margin: 0;
  }
}

.comment_username {
  margin: 0 20px 0 0;
  font: $second-font-size $OSSemibold, Arial, Helvetica, sans-serif;
  color: $primary-text-color;
  time {
    margin: 0 0 0 3px;
  }
}

.reply {
  color: #a88fd8;
  font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
}

.box_comment_plus {
  max-width: 610px;
  float: right;
  margin-bottom: 0 !important;
  .comment_content {
    max-width: 489px;
  }
}

.box_plus {
  width: 93px;
  height: 93px;
  margin: 0 28px 20px 0;
  position: relative;
  display: inline-block;
  text-align: center;
  background: #ececec;
  border: 0px solid;
  border-radius: 55px;
  float: left;
  i {
    position: absolute;
    bottom: 23px;
    left: 32px;
    color: #ffffff;
  }
}
.box_leave-comment{
  h1{
    margin: 0 0 20px 0;
  }
}
.form-comment {
  textarea {
    width: 731px !important;
    height: 217px !important;
  }
  .comment-name,
  .comment-email,
  textarea {
    border: 1px solid #ECECEC;
    border-radius: 3px !important;
    padding: 24px 10px 10px 20px;
    &:focus {
      border: 1px solid #B290DB;
    }
  }
  .comment-name,
  .comment-email,
  textarea,
  label{
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
  }
  textarea {
    margin-bottom: 31px;
  }
  .comment-name,
  .comment-email {
    width: 351px !important;
    padding: 16px 10px 18px 16px;
    margin: 0 0 23px 0;
  }
  .comment-name {
    margin-right: 25px;
  }
  .form-checkbox {
    display: none;
  }
  .form-checkbox + label {
    cursor: pointer;
    margin-top: 9px;
  }
  .form-checkbox:not(checked) + label {
    position: relative;
    padding: 0 0 0 45px;
    top: 17px;
    float: left;
  }
  .form-checkbox:not(checked) + label:before {
    content: '';
    position: absolute;
    top: -7px;
    left: 0;
    width: 31px;
    height: 31px;
    border: 1px solid #ECECEC;
    border-radius: 3px;
    text-align: center;
    vertical-align: top;
  }
  .form-checkbox:checked + label:before{
    content: "\2713";
    position: absolute;
    font-size: 20px;
    color: #acacac;
  }
  input[type="submit"]{
    float: right;
    font: 16px/24px $OSSemibold,Arial,Helvetica,sans-serif;
    color: $second-text-color;
    border: 0 solid;
    border-radius: 5px;
    background: #00ccce;
    padding:16px 65px;
    &:hover{
      background: #00acae;
    }
  }
}
.sidebar {
  padding: 0 0 0 21px;
  margin: -5px 0 0 0;
}

.search {
  margin: 0 0 60px 0;
}

#search {
  position: relative;
  margin: 0;
  a {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 10px;
    color: $third-text-color;
  }
  input[type="search"] {
    width: 271px !important;
    padding: 15px 35px 15px 18px;
    border: 1px solid #EAEAEA;
    border-radius: 2px;
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    box-sizing: border-box;
  }
  input[type="search"]::placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-moz-placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-webkit-input-placeholder {
    color: $third-text-color;
  }
  input[type="search"]:-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-ms-input-placeholder {
    color: $third-text-color;
  }
}
.follow_us {
  margin: 0 0 46px 0;
  h1 {
    margin: 0 0 30px 0;
    font: $primary-heading-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
  }
  a {
    margin: 0 12px 0 0;
    .fa-facebook,
    .fa-twitter,
    .fa-google-plus {
      color: $second-text-color;
      border: 0 solid;
      border-radius: 20px;
    }
    .fa-facebook {
      background: #23599a;
      padding: 8px 13px;
      &:hover {
        background: #5389ca;
      }
    }
    .fa-twitter {
      background: #00c3f5;
      padding: 8px 9px;
      &:hover {
        background: #20e3ff;
      }
    }
    .fa-google-plus {
      background: #ff4b3e;
      padding: 8px 5px;
      &:hover {
        background: #ff7b6e;
      }
    }
  }
}

.subscribe_box {
  margin-bottom: 50px;
  input[type="text"] {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $third-text-color;
    border: 1px solid $input-border-color;
    border-radius: 2px;
    padding: 14px 5px 14px 16px;
    margin: 0 0 30px 0;
    width: 271px !important;
  }
  input[type="text"]::placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-moz-placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-webkit-input-placeholder {
    color: $third-text-color;
  }
  input[type="text"]:-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="submit"] {
    display: inline-block;
    font: 16px/20px $OSRegular, Arial, Helvetica, sans-serif;
    color: $second-text-color;
    background: $buttons-color-turquoise;
    padding: 13px 45px 14px 50px;
    border: 0 solid;
    border-radius: 3px;
    &:hover {
      background: #69ebed;
    }
  }
}

.categories_box {
  margin: 0 0 45px 0;
  @extend .follow_us;
  ul {
    list-style: none;
    padding: 0;
    margin: 0 0 0 20px;
  }
  li::before {
    position: absolute;
    content: '>';
    left: -20px;
    top: 13px;
    line-height: 18px;
    //padding-right:15px;
    font-size: 15px;
    font-weight: bold;
    color: $buttons-color-turquoise;
  }
  li:hover::before {
    color: $buttons-color-turquoise;
    left: -20px;
    top: 13px;
  }
  li {
    position: relative;
    border: 0 solid;
    border-radius: 4px;
    padding: 0 7px;
    &:hover {
      background: #405360;
      //padding-left: 25px;
      a {
        color: $second-text-color;
      }
    }
  }
  a {
    text-decoration: none;
    font: $primary-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    padding: 12px 0;
    display: inline-block;
  }
}

.popular-post_box {
  margin-bottom: 52px;
  font: 13px/22px $OSRegular, Arial, Helvetica, sans-serif;
  color: $primary-text-color;
  @extend .follow_us;
  img,
  p {
    margin: 0 0 34px 0;
  }
  time {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
  }
  a {
    float: right;
    text-decoration: none;
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $link-cl;
    &:hover {
      text-decoration: underline;
    }
  }
}

.recent-post_box {
  margin-bottom: 53px;
  @extend .follow_us;
  ul {
    padding: 0;
    margin: 0 0 0 15px;
    list-style: none;
  }
  li::before {
    content: "\2022";
    position: absolute;
    top: 6px;
    left: -15px;
    padding: 0 17px 0 0;
    color: $buttons-color-turquoise;
    font-size: 30px;
    vertical-align: middle;
  }
  li {
    position: relative;
    vertical-align: middle;
    padding: 0 2px;
    //display: inline-block;
  }
  a {
    font: 14px/26px $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    text-decoration: none;
    display: inline-block;
  }
}

.tags_box {
  @extend .follow_us;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px 8px 0;
  }
  a {
    text-decoration: none;
    font: 14px/36px $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    padding: 8px 15px;
    background: #f8f9fb;
    border: 0 solid;
    border-radius: 4px;
    &:hover {
      background: #00ccce;
      color: $second-text-color;
    }
  }
}
#footer {
  border-top: 1px solid $input-border-color;
  padding: 46px 0 50px 0;
  text-align: center;
  .follow_us {
    margin-bottom: 29px;
  }
}

.copyright {
  font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
  color: #7c7c7c;
  a {
    @extend .copyright;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}
